:root {
	--mainOpacity: rgba(0, 0, 0, 0.486);
	--blackOpacity: rgba(0, 0, 0, 0.849);
	--white: rgb(255, 255, 255);
	--silver: rgb(199, 199, 199);
	--matteblack: rgb(39, 39, 39);
	--lightGrey: rgb(65, 65, 65);
	--black: rgb(0, 0, 0);
	--darkgrey: rgb(37, 37, 37);
	--deepRed: rgb(153, 0, 0);
}

@font-face {
	font-family: 'AMCAP Eternal';
	src: url('../src/AMCAP Eternal.otf') format('truetype');
}

/* Establishes the text and body style of the document */
body {
	margin: 0;
	scroll-behavior: smooth;
	overflow-x: hidden;
	font-family: "AMCAP Eternal";
	align-items: center;
	justify-content: center;
}

/* Animations */

@keyframes smoothScroll {
	0% {
		transform: translateY(0);
	}

	100% {
		transform: translateY(-10px);
	}
}

/* Navigation menu styles */
nav {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: space-between;
	background-color: var(--matteblack);
	box-shadow: 2px 2px 2px 2px var(--mainOpacity);
	color: var(--white);
	padding: 1%;
	padding-left: 1%;
	padding-bottom: 0.5%;
	position: fixed;
	font-size: 1.05em;
	top: 0;
	left: 0;
	width: 100%;
	margin-top: -4px;
	z-index: 1;
	order: 2;
}


/* Navigation menu */
.menu {
	display: flex;
	list-style: none;
	margin: 0;
	padding: 0;
	justify-content: center;
}

.menu li {
	margin: 0 30px;
	margin-left: 20px;
}

.menu a {
	color: var(--silver);
	text-decoration: none;
	padding: 20px;
	transition: all 0.5s;
}

.menu a:hover {
	color: var(--white);
	text-decoration: none;
	padding: 20px;
}

/* Hamburger Menu - Main */

.hamburger {
	display: none;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	cursor: pointer;
	transition: transform 0.3s;
	margin-left: 10px;
	/* Adjust the left margin as needed */
	padding: 15px;
}

.hamburger span {
	background-color: var(--white);
	box-shadow: 1px 1px 1px var(--mainOpacity);
	display: block;
	height: 3px;
	margin: 3px 0;
	width: 20px;
	transition: transform 0.3s, opacity 0.3s;
}

.menu.open {
	opacity: 1;
	visibility: visible;
	transition: opacity 0.3s, visibility 0s linear 0s;
	position: absolute;
	top: 100%;
	left: 0;
	background-color: var(--matteblack);
	padding: 20px;
	box-shadow: 3px 3px 3px var(--mainOpacity);
}

.menu.open li {
	opacity: 1;
	transform: translateY(0);
	transition: opacity 0.3s, transform 0s linear 0s;
}

.menu.open li:nth-child(1) {
	transition-delay: 0.35s;
}

.menu.open li:nth-child(2) {
	transition-delay: 0.45s;
}

.menu.open li:nth-child(3) {
	transition-delay: 0.55s;
}

.menu.open li:nth-child(4) {
	transition-delay: 0.65s;
}

.hamburger.active span:nth-child(1) {
	transform: rotate(45deg) translate(7px, 5px);
}

.hamburger.active span:nth-child(2) {
	opacity: 0;
}

.hamburger.active span:nth-child(3) {
	transform: rotate(-45deg) translate(7px, -5px);
}

/* Parallax container styles */
.parallax-container {
	height: 100vh;
	overflow: hidden;
	margin: 2%;
}

/* Parallax background image styles */
.home {
	background-image: url("./img/CEARCO_Black_Background.jpg");
	background-size: cover;
	background-position: 0%, 50%;
	background-repeat: no-repeat;
	position: fixed;
	height: 100% !important;
	width: 100%;
	overflow: hidden;
	top: 0;
	left: 0;
	z-index: -1;

}

/* Content section styles */
.content {
	position: absolute;
	font-size: 1.8em;
	padding: 1px;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	text-align: center;
	text-shadow: 5px 5px 5px var(--mainOpacity);
	color: var(--white);
	max-width: 90%;
	/* Keeps the width of the paragraph or sentences stable when on desktop */
}

.content h1 {
	font-size: 2.05em;

}

.img-prod {
	border-radius: 30px;
}

strong {
	font-size: 1.5em;
}

/* Button styles */
.learn-more-01 {
	background-color: var(--white);
	color: var(--matteblack);
	text-decoration: none;
	border: none;
	padding: 10px 15px;
	font-size: 0.7em;
	font-family: "AMCAP Eternal";
	border-radius: 5px;
	box-shadow: 3px 3px 3px var(--blackOpacity);
	margin-top: 20px;
	cursor: pointer;
	transition: background-color 0.5s;
	animation: smoothScroll 0.5s ease-in-out forwards;
}

.learn-more-01:hover {
	color: var(--white);
	background-color: var(--matteblack);
}

#about {
	background-image: linear-gradient(60deg,
			var(--matteblack),
			var(--blackOpacity)),
		url(../src/img/ourhistory_bg_01.jpg);
	background-size: cover;
	background-position: 0%, 50%;
	background-attachment: fixed;
	text-align: center;
	height: auto !important;
	width: 100%;
	background-repeat: no-repeat;
	padding-top: 10%;
	padding-bottom: 10%;
	z-index: -1;

}

.about-title {
	font-size: 2.25em;
	text-align: center;
	color: var(--white);
	text-shadow: 3px 3px 3px var(--mainOpacity);
}

.about-paragraph {
	font-size: 1.75em;
	color: var(--white);
	margin: auto;
	max-width: 90%;
	/* Keeps the width of the paragraph or sentences stable when on desktop */
}

/* Pricing Table */
#product {
	background-image: url("./img/CEARCO_Black_Background.jpg");
	background-size: cover;
	background-position: 0%, 50%;
	background-attachment: fixed;
	height: auto !important;
	width: 100%;
	background-repeat: no-repeat;
	opacity: var(--mainOpacity);
	padding-top: 3%;
	padding-bottom: 3%;
}

.product-paragraph {
	text-align: center;
	text-shadow: 12px 12px 12px var(--mainOpacity);
	color: var(--white);
	justify-content: center;
	font-size: 2.5em;
	margin: 3%;
	padding-top: 0;
	padding-bottom: 0;
}

.product-table {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
	/* Create a grid with auto-fit columns */
	grid-gap: 20px;
	/* Add gap between grid items */
	justify-items: center;
	/* Center the grid items horizontally */
	align-items: stretch;
	/* Stretch the grid items vertically */
	z-index: 1;
	transform: scale(0.77);
	margin: 0;
	margin-top: -10vh;
	/* Adjust the margin-top value to bring the product table up more. */

}

.product-plan {
	background-image: linear-gradient(60deg, var(--mainOpacity), var(--silver));
	margin: 10%;
	padding: 5%;
	border-radius: 10px;
	background-color: var(--white);
	justify-content: center;
	box-shadow: 5px 5px 5px var(--mainOpacity);
}

.product-plan h2 {
	padding-left: 0;
	font-size: 2.0em;
	margin-bottom: 0.5em;
}

.product-plan .features {
	list-style: none;
	margin: 0;
	padding: 0;
	text-align: center;
}

.product-plan .features li {
	margin-bottom: 10px;
}

.product-plan .btn {
	display: inline-block;
	padding: 10px 20px;
	font-family: "AMCAP Eternal";
	font-size: 1.5em;
	background-color: var(--matteblack);
	color: var(--white);
	text-decoration: none;
	border-radius: 5px;
	transition: background-color 0.5s;
	cursor: pointer;
}

.product-plan .btn:hover {
	background-color: var(--silver);
	color: var(--matteblack);
}

.product-card {
	display: none;
	background-color: var(--white);
	padding: 5%;
	border-radius: 5px;
	font-size: 1.10em;
	margin: 0;

}

.product-plan .btn:focus+.product-card {
	display: block;
}

/* Footer */

.site-footer {

	background-color: var(--matteblack);
	padding: 0;
	padding-top: 3%;
	padding-bottom: 10%;
	font-size: 18px;
	line-height: 24px;
	color: var(--silver);
}

.site-footer hr {
	border-top-color: #bbb;
	opacity: 0.5;
}

.site-footer hr.small {
	margin: 20px 0;
}

.site-footer h6 {
	color: #fff;
	font-size: 16px;
	text-transform: uppercase;
	margin-top: 5px;
	letter-spacing: 2px;
}

.site-footer a {
	color: var(--silver);
	transition: all 0.5s;
}

.site-footer a:hover {
	color: var(--white);
	text-decoration: none;
}

.footer-links {
	padding-left: 0;
	list-style: none;
}

.footer-links li {
	display: block;
}

.footer-links a {
	color: var(--silver);
}

.footer-links a:active,
.footer-links a:focus,
.footer-links a:hover {
	color: var(--white);
	text-decoration: none;
}

.footer-links.inline li {
	display: inline-block;
}

.site-footer .social-icons {
	text-align: right;
}

.site-footer .social-icons a {
	width: 40px;
	height: 40px;
	line-height: 40px;
	margin-left: 10px;
	margin-right: 0;
	border-radius: 100%;
	background-color: #33353d;
}

.copyright-text {
	margin: 0;
	padding: 2%;
}

/* Scrollbar Webkit*/

::-webkit-scrollbar {
	width: 12px;
}

::-webkit-scrollbar-track {
	background-color: var(--mainOpacity);
}

::-webkit-scrollbar-thumb {
	background-color: var(--matteblack);
}

::-webkit-scrollbar-thumb:hover {
	background-color: var(--darkgrey);
}


/* Media queries for mobile */

@media (max-width: 949px) {

	body {
		overflow-x: hidden;
	}

	main {
		padding-top: 5vh;
		padding-bottom: 20vh;
	}

	/* Media - Navigation menu */
	nav {
		display: flex;
		flex-wrap: wrap;
		margin: auto;
		z-index: 1;
		padding: 0.25%;
		padding-left: 2%;

	}

	.home {
		height: auto;
	}

	.menu {
		width: 100%;
		padding-top: 40%;
		display: none;
		/* Hide menu by default */
	}

	.menu.open {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		background-color: var(--lightGrey);
		padding: 30px;
		position: absolute;
		top: 100%;
		left: 0;
		opacity: 1;
		visibility: visible;
		transition: opacity 0.3s, visibility 0s linear 0s;
	}

	.menu li {
		margin: 10px 0;
	}

	.menu li:hover {
		background-color: var(--darkgrey);
		width: 100%;
		padding: 15px;
	}

	/*  Hamburger Menu - Media  */
	.hamburger {
		display: flex;
		padding-right: 30px;
	}

	.hamburger span {
		background-color: var(--white);
	}

	/*  Parallax container  */

	.parallax-container {
		height: auto;
		margin: auto;
	}

	/*  Media content  */
	.content {
		position: static;
		margin: 20%;
		transform: none;
		font-size: 1.8em;
	}

	.content h1 {
		font-size: 1.8em;
	}

	/**** Image content *****/
	.img-prod {
		width: 100%;
		height: auto;
	}

	.about-paragraph {
		margin: 30px;
	}

	/*  Product content  */
	#product {
		padding-top: 5%;
		padding-bottom: 10%;
	}

	.product-table {
		grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
		grid-gap: 20px;
		transform: scale(1);
		margin: 0;
	}

	.product-plan {
		margin: 15%;
		padding: 5%;
		font-size: 1.2em;
	}

	.product-plan h2 {
		font-size: 1.2em;
	}

	.product-card {
		padding: 10%;
		margin: 0;
		font-size: 0.8em;
	}

	.product-plan .btn {
		font-size: 1.1em;
	}
}

/* Footer */

.site-footer {
	padding-bottom: 0;
}

.site-footer .copyright-text,
.site-footer .social-icons {
	margin-top: 2%;
	text-align: left;
}

.social-icons {
	padding-left: 0;
	margin-bottom: 0;
	list-style: none;
}

.social-icons li {
	display: inline-block;
	margin-bottom: 2px;
}

.social-icons li.title {
	margin-right: 15px;
	text-transform: uppercase;
	color: #96a2b2;
	font-weight: 700;
	font-size: 13px;
}

.social-icons a {
	background-color: #eceeef;
	color: #818a91;
	font-size: 16px;
	display: inline-block;
	line-height: 44px;
	width: 44px;
	height: 44px;
	text-align: center;
	margin-right: 8px;
	border-radius: 100%;
	-webkit-transition: all 0.2s linear;
	-o-transition: all 0.2s linear;
	transition: all 0.2s linear;
}

.social-icons a:active,
.social-icons a:focus,
.social-icons a:hover {
	color: var(--white);
	background-color: var(--silver);
}

.social-icons.size-sm a {
	line-height: 34px;
	height: 34px;
	width: 34px;
	font-size: 14px;
}

.social-icons a.facebook:hover {
	background-color: var(--silver);
}

.social-icons a.instagram:hover {
	background-color: var(--deepRed);
}

@media (max-width: 767px) {

	.parallax-container {
		display: block;
		margin-top: -15%;
	}

	/* Product content */
	.product-paragraph {
		display: block;
		padding: 10%;
	}

	/*  Media content  */
	.content {
		position: static;
		margin: 6%;
		transform: none;
		font-size: 1.5em;
		padding-top: 35%;
	}

	.content h1 {
		font-size: 1.8em;
	}

	.social-icons li.title {
		display: block;
		margin-right: 0;
		font-weight: 600;
	}
}

@media (max-width: 480px) {


	nav {
		display: flex;
		width: 100%;
		align-items: flex-start;
		padding: 10px;
	}

	.content {
		margin: 2%;

	}

	.content h1 {
		font-size: 1.8em;
	}

	.copyright-text {
		padding: 10%;
	}
}